body {
  margin: 10px;
}

form {
  display: flex;
  align-items: flex-end;
  margin-bottom: 30px;
}

input {
  margin-top: 10px;
  border-radius: 5px;
  height: 60px;
  box-sizing: border-box;
  border-color: black;
  cursor: text;
  text-align: center;
}

.submit {
  border-radius: 5px;
  border-color: black;
  background-color: white;
  height: 60px;
  width: 80px;
  cursor: pointer;
}

.submit:hover {
  background-color: rgb(76, 173, 76);
  color: #fbf5f5;
}

.form {
  display: flex;
  flex-direction: column;
  margin-right: 30px;  
}

.watches {
  display: flex;
  flex-wrap: wrap;
}

.clock-container {
  width: 164px;
  margin-right: 20px;
}

h4 {
  text-align: center;
  margin-bottom: 10px;
}

h1 {
  text-align: center;
}

.delete {
  position: relative;
  left: 140px;
  border-radius: 50%;
  border: solid 2px black;
  background-color: white;
  height: 25px;
  width: 25px;
  cursor: pointer;
}

.clock {
  border-radius: 50%;
  border: solid 2px black;
  background: radial-gradient(#fbf5f5, #847b7b);
  /* background: #fff url(/assets/images/posts/clocks/ios_clock.svg) no-repeat center; */
  /* background-size: 88%; */
  height: 10em;
  /* padding-bottom: 31%; */
  position: relative;
  width: 10em;
}

.clock.simple:after {
  background: #000;
  border-radius: 50%;
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 5%;
  height: 5%;
  z-index: 10;
}

.minutes-container, .hours-container, .seconds-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.hours {
  background: #000;
  height: 20%;
  left: 48.75%;
  position: absolute;
  top: 30%;
  transform-origin: 50% 100%;
  width: 2.5%;
}

.minutes {
  background: #000;
  height: 40%;
  left: 49%;
  position: absolute;
  top: 10%;
  transform-origin: 50% 100%;
  width: 2%;
}

.seconds {
  background: #000;
  height: 45%;
  left: 49.5%;
  position: absolute;
  top: 14%;
  transform-origin: 50% 80%;
  width: 1%;
  z-index: 8;
}

@keyframes rotate {
  100% {
    transform: rotateZ(360deg);
  }
}

.hours-container {
  animation: rotate 43200s infinite linear;
}

.minutes-container {
  animation: rotate 3600s infinite steps(60);
}

.seconds-container {
  animation: rotate 60s infinite steps(60);
}
